<template>
  <v-card>
    <v-card-text>
      <p class='text-h6 text--primary'>
        Power Monitor
      </p>
      <line-chart :chart-data="dataCollection"></line-chart>
    </v-card-text>
    
    <v-card-text>
      <p class='text-h6 text--primary'> Time(s) </p>
      <v-container>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Unit On Time</v-col>
          <v-col cols="4" class='text-body-1 d-flex align-center font-weight-bold'> {{OnTime}} </v-col>
          <v-col cols="6"></v-col>
        </v-row>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Transmit Time</v-col>
          <v-col cols="4" class='text-body-1 d-flex align-center font-weight-bold'> {{TxTime}} </v-col>
          <v-col cols="6"></v-col>
        </v-row>
      </v-container>
    </v-card-text>
  </v-card>
</template>


<script>
import LineChart from './LineChart.vue'

export default {
  
  components: {
    LineChart
  },

  methods: {
    createGraphData(output, temperature, alcEnabled, alcAcitve, alcPower) {
        var newLabels = [];
        var i = 0;
        for (i = 0; i < output.length; i++) {
          newLabels.push(i + 1);
        }
        var newData = {
          labels: newLabels,
          datasets: [{
            borderColor: "#33818f",
            label: "Output Power (dBm)",
            borderWidth: 2,
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: output,
            yAxisID: "A"
          },
          {
            borderColor: "#d9a711",
            label: "Temperature (°C)",
            borderWidth: 2,
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: temperature,
            yAxisID: "B"
          }]
        }

        if (alcEnabled && alcAcitve) {
          var calcaulatedAlcPower = alcPower;
          var AlcLine = [];
          for (i = 0; i < output.length; i++) {
            AlcLine.push(calcaulatedAlcPower);
          }

          newData.datasets.push({
            label: "ALC = " + (calcaulatedAlcPower).toFixed(1) + " dBm",
            borderWidth: 5,
            borderColor: "#671191",
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: AlcLine,
            borderDash: [50],
            yAxisID: "A",
          })
        }
        return newData;
    },
  },

  props: {
    OnTime: {
      type: String,
      default: "10 Hour(s) 30 minute(s)"
    },
    TxTime: {
      type: String,
      default: "5 Hour(s) 10 minute(s)"
    }
  },

  data() {
    return {
      dataCollection: null,
    }
  },

  // created: function () {
  //   var output =       [39.0, 38.5, 38.2, 38.1, 37.5, 36.4, 37.2, 39.2, 38.5, 37.5, 38.0];
  //   var temperatures = [56.0, 55.8, 55.3, 56.0, 55.8, 55.0, 55.3, 55.8, 56.1, 55.0, 55.1];
  //   var data = this.createGraphData(output, temperatures, false, false, 0);    
  // }

}
</script>