<!-- Chart Vue JS does not need template since it's being inject via javascript -->

<!---------------------------------------------------------------------------->

<!---------------------------------------------------------------------------->
<script>
import { Line, mixins } from 'vue-chartjs'
const { reactiveProp } = mixins

export default {
  extends: Line,
  mixins: [reactiveProp],
  
  data () {
    return {
      options: {
        scales: {
          yAxes: [{
            id: "A",
            ticks: {
              beginAtZero: true,
              min: 0,
              max: 65,
              stepSize: 5
            },
            gridLines: {
              display: true
            },
            scaleLabel: {
              display: true,
              labelString: "dBm"
            },
            position: "left"
          },
          {
            id: "B",
            ticks: {
              beginAtZero: true,
              min: 0,
              max: 120,
              stepSize: 10
            },
            gridLines: {
              display: false
            },
            scaleLabel: {
              display: true,
              labelString: "°C"
            },
            position: "right"
          }],
          xAxes: [{
            display: true
          }]
        },
        legend: {
          display: true
        },
        responsive: true,
        maintainAspectRatio: false,
        
        tooltips: {enabled: false},
        hover: {mode: null},
      }
    }
  },

  methods: {
    createGraphData(output, temperature, alcEnabled, alcAcitve, alcPower) {
        var newLabels = [];
        var i = 0;
        for (i = 0; i < output.length; i++) {
          newLabels.push(i + 1);
        }
        var newData = {
          labels: newLabels,
          datasets: [{
            borderColor: "#33818f",
            label: "Output Power (dBm)",
            borderWidth: 2,
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: output,
            yAxisID: "A"
          },
          {
            borderColor: "#d9a711",
            label: "Temperature (°C)",
            borderWidth: 2,
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: temperature,
            yAxisID: "B"
          }]
        }

        if (alcEnabled && alcAcitve) {
          var calcaulatedAlcPower = alcPower;
          var AlcLine = [];
          for (i = 0; i < output.length; i++) {
            AlcLine.push(calcaulatedAlcPower);
          }

          newData.datasets.push({
            label: "ALC = " + (calcaulatedAlcPower).toFixed(1) + " dBm",
            borderWidth: 5,
            borderColor: "#671191",
            backgroundColor: "transparent",
            pointBorderColor: "transparent",
            data: AlcLine,
            borderDash: [50],
            yAxisID: "A",
          })
        }
        return newData;
    },
  },

  created: function () {
    var output =       [39.0, 38.5, 38.2, 38.1, 37.5, 36.4, 37.2, 39.2, 38.5, 37.5, 38.0];
    var temperatures = [56.0, 65.8, 70.3, 69.0, 60.8, 57.0, 51.3, 60.8, 62.1, 66.0, 58.1];
    this.chartData = this.createGraphData(output, temperatures, false, false, 0);    
  }
}
</script>

