<template>
  <v-card>
    <v-card-text>
      <p class='text-h6 text--primary'> Clock (NTP) </p>

      <v-container>
        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Primary </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{PrimaryAddress}} </v-col>
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense type="text" label="Primary NTP Address"
              :rules="[IsValidIp(new_PrimaryAddress)]" v-model="new_PrimaryAddress"></v-text-field>
          </v-col>
          <v-col cols="1"></v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Secondary </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{SecondaryAddress}} </v-col>
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense type="text" label="Secondary NTP Address"
              :rules="[IsValidIp(new_SecondaryAddress)]" v-model="new_SecondaryAddress"></v-text-field>
          </v-col>
          <v-col cols="1"></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Manual Date/Time </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{ManualTimeEnabled ? "Manual" : "NTP Server"}}
          </v-col>

          <v-col cols="2" align-self="center">
            <v-btn-toggle color="primary" mandatory v-model="new_ManualDateTimeEnabled" >
              <v-btn outlined :value="false">
                NTP Server
                <v-icon dark right> mdi-server </v-icon>
              </v-btn>
              <v-btn outlined :value="true"> 
                Manual
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"
            v-if="new_ManualDateTimeEnabled"> 
            {{ManualDateTime}} 
          </v-col>
          <v-col cols="2" align-self="center"
            v-if="new_ManualDateTimeEnabled">
            <v-text-field class="text-body-1 font-weight-medium" dense type="text" label="yyyy-mm-dd HH:mm:ss"
              v-model="new_ManualDateTime"></v-text-field>
          </v-col>

        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> UTC Offset </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> 
            {{UtcOffsetHour}} Hour(s) {{UtcOffsetMinute}} Minute(s)
          </v-col>
          <v-col cols="1" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense type="text" label="Hour(s)"
              v-model="UtcOffsetHour"></v-text-field>
          </v-col>
          <v-col cols="1" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense type="text" label="Minute(s)"
              v-model="UtcOffsetMinute"></v-text-field>
          </v-col>
          
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Update Daily </v-col>
          <v-col cols="1" align-self="center" class="text-body-1 d-flex justify-start font-weight-bold"> {{UpdateDaily ? "Yes" : "No"}} </v-col>
          <v-col cols="3" align-self="center">
            <v-btn-toggle color="primary" mandatory v-model="new_UpdateDaily">
              <v-btn outlined :value="true"> 
                Yes
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false">
                No 
                <v-icon dark right> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
            
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="font-weight-medium" color="primary">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>

  </v-card>
</template>



<script>

export default {
  name: 'ClockNtp',

  props: {
    PrimaryAddress: {
      type: String,
      default: "192.168.100.10"
    },
    SecondaryAddress: {
      type: String,
      default: "192.168.200.10"
    },
    ManualTimeEnabled: {
      type: Boolean,
      default: false
    },
    ManualDateTime: {
      type: String,
      default: "2020-01-23 23:57:03"
    },
    UpdateDaily: {
      type: Boolean,
      default: false
    },
    UtcOffsetHour: {
      type: Number,
      default: 1
    },
    UtcOffsetMinute: {
      type: Number,
      default: 30
    }
  },

  data: function() {
    return {
      new_PrimaryAddress: this.PrimaryAddress,
      new_SecondaryAddress: this.SecondaryAddress,
      
      new_ManualDateTimeEnabled: this.ManualTimeEnabled,
      new_ManualDateTime: this.ManualDateTime,
      
      new_UpdateDaily: this.UpdateDaily,
      
      new_UtcOffsetHour: this.UtcOffsetHour,
      new_UtcOffsetMinute: this.UtcOffsetMinute,

    }
  },

  methods: {
    IsValidIp(addr) {
      var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (addr.match(ipformat))
      {
        return true;
      }
      return false;
    },
  }
}

</script>