<template>
  <v-card flat>
    <v-card-text>
      <p class='text-h6 text--primary'>
        Unit Information
      </p>

      <v-container>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Model</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Model}} </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Firmware</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Firmware}} </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Serial</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Serial}} </v-col>
          
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Bootloader</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Bootloader}} </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>

    <v-card-text>
      <p class='text-h6 text--primary'> Status </p>

      <v-container>
        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Mode</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Mode}} </v-col>
        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Temperature</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Temperature}} </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Power</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Power}} </v-col>
          
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Fan</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Fan}} </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Tx Reference</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{TxRef}} </v-col>
        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Compliance</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{IsCompliant}} </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Attenuator</v-col>
          <v-col align-self="center" cols="1" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Attenuator).toFixed(2)}} dB </v-col>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center'>
            <v-text-field type="number" suffix="dB"
              :rules="[StepAttenIsValid(new_StepAtten)]" :label="StepAttenIncrementString" v-model="new_StepAtten"> </v-text-field>  
          </v-col>
          
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          <v-col align-self="center" cols="1" class='text-body-1 d-flex align-center font-weight-bold'> {{SwEnabled ? "Unmuted" : "Muted"}} </v-col>

          <v-col align-self="center" cols="2">
            <v-btn-toggle color="primary" mandatory v-model="new_SwEnable">
              <v-btn outlined :value="true"> 
                Unmute
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false">
                Mute 
                <v-icon dark right> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>

        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="mx-1" color="primary"
            :disabled="IsDifferent == false" v-on:click="SetChanges">Submit </v-btn>
          </v-col>
        </v-row>
        
      </v-container>
      
    </v-card-text>
  </v-card>
</template>


<script>

export default {
  name: 'Status',

  props: {
    Mode: {
      type: String,
      default: "Active"
    },
    Power: {
      type: String,
      default: "38.5 dBm"
    },
    Temperature: {
      type: String,
      default: "55.4 C"
    },
    Fan: {
      type: String,
      default: "3490 RPM"
    },

    TxRef: {
      type: String,
      default: "Locked"
    },
    IsCompliant: {
      type: String,
      default: "Enabled"
    },

    // TX Attenuator Information
    Attenuator: {
      type: Number,
      default: 25.5
    },
    AttenIncrement: {
      type: Number,
      default: 0.25
    },
    MaxAtten: {
      type: Number,
      default: 31.75
    },
    MinAtten: {
      type: Number,
      default: 0
    },

    SwEnabled: {
      type: Boolean,
      default: true
    },

    Model: {
      type: String,
      default: "Model Number"
    },
    Serial: {
      type: String,
      default: "Serial Number"
    },
    Firmware: {
      type: String,
      default: "Firmware"
    },
    Bootloader: {
      type: String,
      default: "0.2.2"
    }

  },

  computed: {
    StepAttenIncrementString() {
      return "Step Increment is " + parseFloat(this.AttenIncrement).toFixed(2) + "dB";
    },
    
    IsDifferent() {
      return this.new_StepAtten != this.Attenuator
        || this.new_SwEnable != this.SwEnabled;
    }
  },

  methods: {
    StepAttenIsValid(atten) {
      return atten >= this.MinAtten
        && atten <= this.MaxAtten;
    },

    SetChanges() {
      this.Attenuator = this.new_StepAtten;
      this.SwEnabled = this.new_SwEnable;
    }
  },

  data: function() {
    return {
      new_StepAtten : this.Attenuator,
      new_SwEnable: this.SwEnabled
    }
  }

}
</script>